mainMenu('.nav-list','.btn-menu'); 
function mainMenu(menu,btn){ 
	var menu = $(menu), 
	btn = $(btn); 
	btn.on('click',function(){ 
		btn.blur(); 
		if(btn.hasClass('disable')){ 
			btn.removeClass('disable') 
			btn.attr("data-disable","false") 
			menu.removeClass('disable'); 
			menu.slideDown(300); 
		}else{ 
			btn.addClass('disable') 
			btn.attr("data-disable","true") 
			menu.addClass('disable'); 
			menu.slideUp(300); 
		} 
	}) 
	$(btn).attr("data-disable","true"); 
	toggleMenu(btn,menu); 
	$(window).resize(function(){ 
		toggleMenu(btn,menu); 
	}) 
	function toggleMenu(btn,menu){ 
		if($(btn).is(":visible") && $(btn).attr("data-disable")=="true"){ 
			$(btn).addClass('disable'); 
			$(menu).addClass('disable'); 
			$(menu).hide(); 
		}else if(!$(btn).is(":visible")){ 
			$(btn).removeClass('disable'); 
			$(menu).removeClass('disable'); 
			$(menu).show(); 
		} 
	} 
}

function replaceNav(){
	var $btn = $('.btn-menu'),
			$nav = $('.nav'),
			$header = $('.header');

	var replace = function(){
		if ($(window).outerWidth() < 768 && $nav.length == 1) {
			$btn.after($nav);
		}else{
			$header.after($nav);
		}
	}
	replace();

	$(window).resize(function(){
		replace();
	})
}

replaceNav();